import React, {useEffect, useState} from 'react';
import Logo from '../../assets/logo.svg'
import {motion, useViewportScroll} from "framer-motion";
import {Link, useLocation} from "react-router-dom";

const Navbar = () => {

    const [rotate, setRotate] = useState(window.scrollY)

    const location = useLocation();

    const transition = {duration: 0.8, ease: [0.6, 0.01, -0.05, 0.9]};


    const {scrollY} = useViewportScroll()


    useEffect(() => {

        scrollY.onChange(v => setRotate(v));


    }, [scrollY]);


    return (
        <header className="fixed top-0 left-0 w-full z-[1] mix-blend-difference text-white">
            <nav className="container mx-auto py-4 lg:py-6">
                <ul className="flex justify-between items-center h-full ">
                    <li>
                        <Link to="/" className="link-underline">
                            Projects.
                        </Link>
                    </li>
                    <li onClick={() => location.pathname === "/" ? window.scrollTo({
                        top: 0,
                        behavior: 'smooth'
                    }) : null}
                    >
                        <Link to="/">
                            <motion.img
                                className="h-[80px] md:h-[100px] lg:h-[110px] xl:h-[120px] transition-[height] duration-300"
                                key={location.pathname}
                                src={Logo}
                                alt="Logo"
                                initial={{
                                    rotate: 0,
                                }}
                                animate={{
                                    rotate: 180,
                                    transition: {delay: 0.2, ...transition}
                                }}
                                style={{ height: rotate > 100 ? "60px" : "" }}
                            />
                        </Link>
                    </li>
                    <li>
                        <Link to="/about-me" className="link-underline">
                            About me.
                        </Link>
                    </li>
                </ul>
            </nav>
        </header>
    );
};

export default Navbar;