import React, {useEffect, useRef, useState} from 'react';
import {ProjectsData} from "./ProjectsData";
import {Link} from "react-router-dom";
import {motion, useViewportScroll} from "framer-motion";

const Projects = () => {

    const boxRef = useRef([]);

    const [selectedId, setSelectedId] = useState(null)

    const ProjectItemsCoords = () => {
        const array = [];

        let i = 0;
        while (i < ProjectsData.length) {
            array.push({x: 0, y: 0, w: 0, h: 0});
            i++;
        }
        return array;
    }
    const [coords, setCoords] = useState(ProjectItemsCoords);


    const getPosition = () => {

        const newState = coords.map((obj, key) => {
            return {
                ...obj,
                x: boxRef.current[key].offsetLeft,
                y: boxRef.current[key].getBoundingClientRect().top,
                h: boxRef.current[key].offsetHeight,
                w: boxRef.current[key].offsetWidth
            };
        });

        setCoords(newState);

    };


    // Get the position of the red box in the beginning
    useEffect(() => {

        getPosition()

    }, []);

    // Re-calculate X and Y of the red box when the window is resized by the user
    useEffect(() => {

        window.addEventListener("resize", getPosition);
        window.addEventListener("scroll", getPosition);

        return () => {
            window.removeEventListener('resize', getPosition);
            window.removeEventListener('scroll', getPosition);
        };

    }, []);

    const transition = {duration: 0.2, ease: [0.6, 0.01, -0.05, 0.9]};
    const transitionInitial = {duration: 0.8, ease: [0.6, 0.01, -0.05, 0.9]};

    const {scrollYProgress} = useViewportScroll()
    // const opacity = useTransform(scrollYProgress, [0, 0.25], [1, 0])

    return (
        <section
            className="grid grid-cols-1 gap-y-12 lg:gap-y-0">
            {ProjectsData.map((item, key) => {
                return (
                    <div key={key} className="lg:h-screen">
                        <Link
                            className="group cursor-zoom-in flex"
                            to={{
                                pathname: item.href, state: {
                                    coords: {
                                        x: coords[key].x, y: coords[key].y
                                    }, boxSize: {
                                        h: coords[key].h, w: coords[key].w
                                    },
                                },
                            }}
                        >
                            <motion.article onClick={() => setSelectedId(key)}
                                            className={`lg:project ${item.img} h-[25vh] lg:h-[70vh] bg-cover bg-top w-full flex items-center text-center p-5`}
                                            initial={{
                                                opacity: 0, y: -40
                                            }}
                                            animate={{
                                                y: 0, opacity: 1, transition: {...transitionInitial},
                                            }}
                                            exit={{
                                                opacity: selectedId === key ? 1 : 0, transition: {...transition},
                                            }}
                                            ref={(element) => {
                                                boxRef.current[key] = element;
                                            }}

                                // style={{opacity: coords[key].y / 100 }}
                            >
                                <motion.div
                                    className="w-full relative"
                                    initial={{
                                        opacity: 0, y: -20, transition: {...transition},
                                    }}
                                    animate={{
                                        y: 0, opacity: 1, transition: {delay: 0.2, ...transitionInitial},
                                    }}
                                    exit={{
                                        y: selectedId === key ? -20 : 0, opacity: 0, transition: {...transition},
                                    }}
                                >

                                    <h4 className="uppercase text-white font-light text-base lg:text-2xl">{item.category}</h4>
                                    <h3 className="uppercase text-white font-bold text-2xl lg:text-7xl mb-3">{item.name}</h3>
                                    <div
                                        className="opacity-0 translate-y-4 group-hover:translate-y-0 group-hover:opacity-100 duration-200 absolute mx-auto w-full">
                                        <span className="uppercase text-white font-light link-underline after:bg-white">
                                            see project
                                        </span>
                                    </div>
                                    {/*{coords[key].y}*/}
                                </motion.div>

                            </motion.article>
                        </Link>
                    </div>
                )
            })}
        </section>
    );
};

export default Projects;