import React from 'react';
import {Link} from "react-router-dom";
import {ProjectsData} from "../pages/projects/ProjectsData";

const Pagination = ({id}) => {
    return (
        <section className="flex justify-between container mx-auto uppercase my-12">
            <div>
                {
                    ProjectsData[id - 1] === undefined ? null :
                        <>
                            prev
                            <br/>
                            <h3 className="text-2xl sm:text-3xl md:text-4xl lg:text-4xl font-extrabold link-underline after:h-1 after:-bottom-3 after:bg-black">
                                <Link to={ProjectsData[id - 1].href}>{ProjectsData[id - 1].name}</Link>
                            </h3>
                        </>
                }
            </div>
            <div className="text-end">
                {
                    ProjectsData[id + 1] === undefined ? null :
                        <>
                            next
                            <br/>
                            <h3 className="text-2xl sm:text-3xl md:text-4xl lg:text-4xl font-extrabold link-underline after:h-1 after:-bottom-3 after:bg-black">
                                <Link to={ProjectsData[id + 1].href}>{ProjectsData[id + 1].name}</Link>
                            </h3>
                        </>
                }
            </div>
        </section>
    );
};

export default Pagination;