import React from 'react';
import {motion} from "framer-motion";

const Description = ({body, variant}) => {

    const transition = {duration: 0.4, ease: [0.43, 0.13, 0.23, 0.96]};

    return (
        <motion.section
            className={`text-center flex flex-col justify-center items-center ${variant}`}
            initial={{
                y: -40,
                opacity: 0,
            }}
            animate={{
                y: 0,
                opacity: 1,
                transition: {...transition},
            }}
            exit={{
                opacity: 0,
                y: -40
            }}
            transition={transition}
        >
            {body}
        </motion.section>
    );
};

export default Description;