import React, {useEffect, useState} from 'react';
import {motion} from "framer-motion";
import {useLocation} from "react-router-dom";
import Pagination from "../../pagination/Pagination";

const Project = ({data, id}) => {

    const location = useLocation()

    const [canScroll, setCanScroll] = useState(false)

    const transition = {duration: 0.8, ease: [0.6, 0.01, -0.05, 0.9]};
    const transition2 = {duration: 0.4, ease: [0.43, 0.13, 0.23, 0.96]};

    const transitionData = (
        location.state === undefined ?
            {
                opacity: 0,
                width: "100%",
                height: "100vh",
                y: 0,
                x: 0,
                backgroundPosition: "top",
                backgroundSize: "cover",
                transition: {...transition},
            }
            :
            {
                width: location.state.boxSize.w,
                height: location.state.boxSize.h,
                backgroundPosition: "top",
                backgroundSize: "cover",
                x: location.state.coords.x,
                y: location.state.coords.y,
            }
    )

    useEffect(() => {
        if (canScroll === false) {
            document.querySelector("body").style.overflowX = "hidden"
        } else {
            document.querySelector("body").style.overflowX = "unset"
        }

    }, [canScroll])

    return (
        <section>
            <motion.div
                onAnimationComplete={() => setCanScroll(true)}
                className={`${data.img} h-screen`}
                initial={transitionData}
                animate={{
                    opacity: 1,
                    width: "100%",
                    height: "100vh",
                    y: 0,
                    x: 0,
                    backgroundPosition: "top",
                    backgroundSize: "cover",
                    transition: {...transition},
                }}
                // exit={{
                //     opacity: 0
                // }}
            >
                <motion.div
                    className="text-white relative container mx-auto flex flex-col justify-center text-center h-full"
                    initial={{
                        opacity: 0,
                        y: 40
                    }}
                    animate={{
                        y: 0,
                        opacity: 1,
                        transition: {delay: 0.8, ...transition2},
                    }}
                    exit={{
                        opacity: 0,
                        y: -40
                    }}
                    transition={transition2}
                >
                    <h2 className="uppercase text-base lg:text-2xl font-light mb-3">{data.category}</h2>
                    <h1 className="uppercase text-3xl sm:text-4xl md:text-5xl lg:text-6xl xl:text-8xl font-bold">{data.name}</h1>

                    <motion.div
                        className="absolute bottom-32 right-0 mx-auto w-full"
                        initial={{
                            opacity: 0,
                            y: 40
                        }}
                        animate={{
                            y: 0,
                            opacity: 1,
                            transition: {delay: 1.2, ...transition2},
                        }}
                    >
                        <div
                            className="border-2 inline-flex rounded-full cursor-pointer"
                            onClick={() => document.getElementById("project").scrollIntoView({
                                behavior: 'smooth'
                            })}
                        >
                            <div className="text-6xl -mt-7 animate-bounce inline-flex">
                                &#8228;
                            </div>
                        </div>
                    </motion.div>
                </motion.div>

            </motion.div>
            <motion.div
                id="project"
                className="container mx-auto py-12"
                initial={{
                    opacity: 0
                }}
                animate={{
                    opacity: 1,
                    transition: {delay: 0.8},
                }}
            >

                <div className="flex flex-row flex-wrap lg:mx-20">
                    <div className="lg:basis-2/5">
                        <dl>
                            <div className="grid grid-cols-2 my-4">
                                <dt className="">Client</dt>
                                <dd className="font-light">{data.name}</dd>
                            </div>
                            <div className="grid grid-cols-2 my-4">
                                <dt className="">Project</dt>
                                <dd className="font-light">{data.category}</dd>
                            </div>
                            <div className="grid grid-cols-2 my-4">
                                <dt className="">Role</dt>
                                <dd className="font-light">{data.role}</dd>
                            </div>
                            <div className="grid grid-cols-2 my-4">
                                <dt className="">Specs</dt>
                                <dd className="font-light">{data.specs}</dd>
                            </div>
                            <div className="grid grid-cols-2 my-4">
                                <dt className="">Link</dt>
                                <dd className="font-bold flex">
                                    <a href={data.link} target="_blank" rel="noreferrer"
                                       className="link-underline after:bg-black text-base">
                                        Visit the website
                                    </a>
                                    <span className="link-outside"/>
                                </dd>
                            </div>
                        </dl>
                    </div>
                    <div className="lg:basis-3/5 my-4">
                        <p className="font-light lg:pl-12">
                            {data.description}
                        </p>
                    </div>
                </div>

                <div className="pt-12">
                    {
                        data.images.map((i, k) => {
                            return (
                                <img key={k} src={i.src} alt={i.desc} className="mb-12 lg:mb-24"/>
                            )
                        })
                    }
                </div>

                <Pagination id={id}/>
            </motion.div>

        </section>
    );
};

export default Project;