import React from 'react';
import Description from "../../Description";
import {Link} from "react-router-dom";

const NotFound = () => {
    return (
        <>
            <Description
                variant="h-[85vh]"
                body={
                    <>
                        <h1 className="text-black text-4xl sm:text-5xl md:text-6xl lg:text-8xl font-bold ">
                            404
                        </h1>
                        <h2 className="text-black text-base lg:text-4xl my-3 font-light">Page not found</h2>
                        <Link className="mt-4" to="/">Back to home page</Link>
                    </>
                }
            />
        </>
    );
};

export default NotFound;