import React from 'react';
import Description from "../../Description";
import IconGit from "../../../assets/icon-github.svg";
import IconIn from "../../../assets/icon-linkedin.svg";
import {motion} from "framer-motion";
import Me from '../../../assets/about-me/me3.jpg'

const transition = {duration: 0.2, ease: [0.43, 0.13, 0.23, 0.96]};

const AboutMe = () => {
    return (
        <section>
            <div className="container mx-auto text-center mt-[150px] lg:mt-[200px]">
                <Description body={
                    <>

                        <img height="250" width="250" className=" rounded-full overflow-hidden mb-8"
                             src={Me} alt="Micha Baczyski"
                        />
                        <h1 className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-extrabold">
                            About me.
                        </h1>
                    </>
                }
                />
                <motion.article
                    className="max-w-3xl mx-auto"
                    initial={{
                        y: -40,
                        opacity: 0,
                    }}
                    animate={{
                        y: 0,
                        opacity: 1,
                        transition: {delay: 0.2, ...transition},
                    }}
                    exit={{
                        opacity: 0,
                        y: -40
                    }}
                    transition={transition}
                >
                    <p className="mt-8">
                        Hello there :)
                        <br/>
                        Currently I'm learning Front-End Development, and searching for a first job as a junior front-end
                        developer.
                        {/*Working for year as a freelancer.*/}
                        <br/>
                        Graduating in this year (2022).
                    </p>

                    <h2 className="font-bold mt-8 mb-2">Stack</h2>
                    <p className="max-w-lg mx-auto">
                        HTML, CSS (SCSS, SASS), Javascript (React.js, Node, Axios), SQL, Adobe XD, Figma, Wordpress, GIT
                    </p>


                    <ul className="flex gap-5 justify-center my-12">
                        <li>
                            <a href="https://github.com/ScavengerRat" target="_blank" rel="noreferrer">
                                <img src={IconGit} alt="Github icon link"/>
                            </a>
                        </li>
                        <li>
                            <a href="https://www.linkedin.com/in/micha%C5%82-baczy%C5%84ski-05b89520a/" target="_blank"
                               rel="noreferrer">
                                <img src={IconIn} alt="Linkedin icon link"/>
                            </a>
                        </li>
                    </ul>

                </motion.article>

            </div>
        </section>
    );
};

export default AboutMe;